* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  color: #484848;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.app .header {
  width: 100%;
  background-color: #f1f1f1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 50px 10px;
}
.app .header .title {
  margin: 0 auto;
}
.app .header h1 {
  font-size: 40px;
}
.app .header h2 {
  font-weight: 400;
}
.app .header .links {
  margin: 30px auto 0 auto;
  display: grid;
  align-items: center;
  grid-template-columns: auto auto auto auto;
  gap: 20px;
}
.app .header .links a {
  margin: 0 auto;
  text-decoration: none;
  display: flex;
  align-items: center;
  color: #484848;
  opacity: 0.7;
  color: #000;
  transition: opacity 0.3s;
  font-weight: bold;
}
.app .header .links a:hover {
  opacity: 1;
}
.app .header .links a svg {
  width: 30px;
  height: 30px;
}
.app .portfolio {
  margin-top: 50px;
}
.app .portfolio h2 {
  text-align: center;
  padding-bottom: 20px;
  font-weight: normal;
}
.app .portfolio .screens {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.app .portfolio .screen {
  width: 100%;
  max-width: 500px;
  margin: 20px;
  display: flex;
  justify-content: center;
}
@media (max-width: 768px) {
  .app .portfolio .screen {
    margin: 0 0 30px 0;
  }
  .app .portfolio .screen img {
    width: 95%;
  }
}
.app .portfolio .screen img {
  width: 100%;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.app .portfolio .other {
  text-align: center;
  width: 100%;
  max-width: 500px;
  margin-inline: auto;
  margin-bottom: 50px;
}
.app .portfolio .other .list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.app .portfolio .other .list img {
  padding: 20px;
  width: 200px;
}
.app .footer {
  margin-top: 100px;
  background-color: #f1f1f1;
  height: 25px;
  width: 100%;
  display: block;
}

